@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot?11f8zo");
  src: url("../fonts/icomoon.eot?11f8zo#iefix") format("embedded-opentype"),
    url("../fonts/icomoon.ttf?11f8zo") format("truetype"),
    url("../fonts/icomoon.woff?11f8zo") format("woff"),
    url("../fonts/icomoon.svg?11f8zo#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

